          <div slot="header">
            <span>设置</span>
          </div>
          <el-tabs>
            <el-tab-pane label="修改密码">
              <el-form :model="dialog" ref="dialog" :rules="dialogRules">
                <el-form-item label="原密码" prop="origin">
                  <el-input type="password" v-model="dialog.origin" maxlength="16"></el-input>
                </el-form-item>
                <el-form-item label="新密码" prop="newPass">
                  <el-input type="password" v-model="dialog.newPass" maxlength="16"></el-input>
                </el-form-item>
                <el-form-item label="重复密码" prop="repeat">
                  <el-input type="password" v-model="dialog.repeat" maxlength="16"></el-input>
                </el-form-item>
                <el-button type="primary" class="sub-button" @click="handlePass">提交</el-button>
