<script>
import store from "../store/index";
export default {
  name: "UserCenter",

  data() {
    var validateOrigin = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      let patten = /^[\w_-]{6,16}$/;
      if (!patten.test(value)) {
        callback(new Error("密码不符合"));
      } else {
        if (this.dialog.repeat !== "") {
          this.$refs.dialog.validateField("repeat");
        }
        callback();
      }
    };
    var validateRepeat = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.dialog.newPass) {
        callback(new Error("两次密码不一致"));
      } else {
        callback();
      }
    };
    return {
      dialog: {
        origin: "",
        newPass: "",
        repeat: ""
      },
      dialogRules: {
        origin: [{ validator: validateOrigin, trigger: "blur" }],
        newPass: [{ validator: validatePass, trigger: "blur" }],
        repeat: [{ validator: validateRepeat, trigger: "blur" }]
      },
      username: store.getters.name,
      avatar: store.getters.avatar
    };
  },
  methods: {
    handlePass() {
      this.$refs["dialog"].validate(valid => {
        if (valid) {
          this.$message({
            message: "Demo: 密码未被修改",
            type: "success"
          });
          this.$store.dispatch("user/logout");
          this.$router.push({ path: "/login" });
        } else {
          return false;
        }
      });
    }
  }
};
</script>

